import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';
Vue.use(Vuex);
Vue.prototype.$http = axios.create({
    baseURL: 'https://cnodejs.org/api/v1'
})
const store = new Vuex.Store({
    state: {
        topics: [
            { name: 'all', desc: '' },
            { name: 'good', desc: '' },
            { name: 'share', desc: '' },
            { name: 'ask', desc: '' },
            { name: 'job', desc: '' },
        ],
        isLogined: false, //
        isShowMore: false, //actionsheet
        loginName: '', //
        accessToken: '',
        unLoginedMoreMenus: {
            login: '',
        },
        LoginedMoreMenus: {
            center: '',
            msg: '',
            logout: ''
        }
    },
    getters: {
        moreMenus: function(state) {
            if (state.isLogined) {
                return state.LoginedMoreMenus;
            }
            return state.unLoginedMoreMenus;
        }
    },
    mutations: {
        mutationIsShowMore: function(state, payload) {
            // statestorestatepayload
            state.isShowMore = payload
        },
        mutationIsLogin(state) {
            state.isLogined = true
        },
        mutationIsLoginName(state, payload) {
            state.loginName = payload
        },
        mutationAccessToken(state, payload) {
            state.accessToken = payload
        },
        mutationLogout(state) {
            state.isLogined = false;
            state.loginName = '';
            state.accessToken = '';
        }
    },
    actions: {}
})
export default store;


